"use client";

import React, { useState, useEffect } from "react";
import { signIn, useSession } from "next-auth/react";
import { useRouter } from "next/navigation";
import { Lock, Mail, Loader2 } from "lucide-react";

export default function LoginPage() {
  const router = useRouter();
  const { data: session, status } = useSession();
  
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState("");

  // Redirect if already logged in
  useEffect(() => {
    if (status === "authenticated") {
      router.push("/admin/dashboard");
    }
  }, [status, router]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setError("");

    try {
      const res = await signIn("credentials", {
        email: email.toLowerCase().trim(),
        password: password,
        redirect: false, // Important: We handle redirect manually to avoid window errors
      });

      if (res?.error) {
        setError("Invalid credentials. Access Denied.");
        setIsLoading(false);
      } else {
        // Safe redirect using Next.js Router
        router.push("/admin/dashboard");
        router.refresh();
      }
    } catch (err) {
      setError("A system error occurred. Please try again.");
      setIsLoading(false);
    }
  };

  if (status === "loading") return <div className="min-h-screen bg-[#121212] flex items-center justify-center text-white">Initializing...</div>;

  return (
    <div className="min-h-screen bg-[#121212] flex items-center justify-center p-4">
      <div className="w-full max-w-md bg-[#1c1c1c] border border-white/10 p-8 rounded-2xl shadow-2xl">
        <div className="text-center mb-8">
          <h1 className="text-3xl font-bold text-white font-serif mb-2">System Login</h1>
          <p className="text-gray-400 text-sm">Authorized Personnel Only</p>
        </div>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <label className="text-xs font-bold text-gray-500 uppercase tracking-widest ml-1">Identity (Email)</label>
            <div className="relative">
              <Mail className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500" size={18} />
              <input
                required
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="w-full bg-white/5 border border-white/10 rounded-xl py-3 pl-10 pr-4 text-white focus:ring-2 focus:ring-amber-600 outline-none transition-all"
                placeholder="nikkitachoudhary306@gmail.com"
              />
            </div>
          </div>

          <div className="space-y-2">
            <label className="text-xs font-bold text-gray-500 uppercase tracking-widest ml-1">Access Key (Password)</label>
            <div className="relative">
              <Lock className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500" size={18} />
              <input
                required
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="w-full bg-white/5 border border-white/10 rounded-xl py-3 pl-10 pr-4 text-white focus:ring-2 focus:ring-amber-600 outline-none transition-all"
                placeholder="••••••••"
              />
            </div>
          </div>

          {error && <div className="text-rose-500 text-xs font-medium text-center bg-rose-500/10 py-2 rounded-lg">{error}</div>}

          <button
            disabled={isLoading}
            type="submit"
            className="w-full py-3 bg-amber-600 hover:bg-amber-500 text-white font-bold rounded-xl mt-4 transition-all flex items-center justify-center gap-2 disabled:opacity-50 disabled:cursor-not-allowed shadow-lg shadow-amber-600/20"
          >
            {isLoading ? <Loader2 className="animate-spin" size={20} /> : "Authenticate"}
          </button>
        </form>
      </div>
    </div>
  );
}